export default {
	data() {
		return {
			mobile: '',
			code: '',
			timer: null,
			time: 60,
			btn: '获取验证码'
		};
	},
	onUnload() {
		clearInterval(this.timer);
	},
	methods: {
		inputMobile(e) {
			this.mobile = e.detail;
		},
		getCode() {
			console.log('获取验证码');
			if (this.time === 60) {
				this.$Fetch({
					url: 'user/code',
					data: { mobile: this.mobile },
					success: res => {
						console.log(res);
						this.code = res.code;
						uni.showToast({
							title: '发送成功',
							icon: 'success'
						});
						this.timer = setInterval(() => {
							this.time--;
							this.btn = this.time + 's后重新获取';
							if (this.time < 0) {
								clearInterval(this.timer);
								this.timer = null;
								this.time = 60;
								this.btn = '获取验证码';
							}
						}, 1000);
					}
				});
			}
		},
		onSubmit(e) {
			console.log('提交数据：', e.detail.value);
			let data = e.detail.value;
			if (data.code === this.code) {
				this.$Fetch({
					url: 'login/change_mobile',
					data: data,
					success: res => {
						setTimeout(() => {
							uni.navigateBack({});
						}, 1500);
						uni.showToast({
							icon: 'success',
							title: '修改成功'
						});
					}
				});
			}else{
				uni.showToast({
					icon: 'none',
					title: '验证码错误'
				});
			}
		}
	}
};
